.topicContainer
{
	border: 1px solid #dba3ff;
	padding:20px;
	margin-top:4px;
	margin-bottom: 4px;
	background-color: white;
	color:black;
	-webkit-box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
	        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
	border-radius: 5px;
	cursor: pointer;
}

.topicContainer:active 
{ 
    -webkit-transform: translateY(5px); 
        -ms-transform: translateY(5px); 
            transform: translateY(5px);
    -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.2);

}

.topicContainer:hover
{ 
    background-color: #f2e3fc;

}

.selected-answer
{
	border: 1px solid #009ec1;
	background-color: #00d0ff;	
}


